<template>
  <div>
    <Tabs
      :dataSource="tabs"
      :defaultTab="defaultTab"
      @onClick="handleClick"
    ></Tabs>
    <router-view></router-view>
  </div>
</template>

<script>
import Tabs from "@/components/Tabs";
export default {
  components: {
    Tabs,
  },
  data() {
    return {
      defaultTab: "",
      tabs: [
        {
          key: "policy",
          name: "政策說明",
        },
        {
          key: "service",
          name: "服務條款",
        },
        {
          key: "privacy",
          name: "隱私權政策",
        },
        {
          key: "disclaimer",
          name: "免責聲明",
        },
      ],
    };
  },
  methods: {
    handleClick(key) {
      this.$router.push(`/system/${key}`);
    },
  },
  watch: {
    "$route.path": {
      immediate: true,
      handler(value) {
        this.defaultTab = value.replace("/system/", "");
      },
    },
  },
};
</script>

<style></style>
